import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import backgroundImage from './background1.jpg'; // Import the image

// Set CSS variable for background image
document.body.style.setProperty('--background-image', `url(${backgroundImage})`);

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);


