import React from "react";
import logo from "./logo.jpg";
import "./App.css";
import { Getquote } from "./components/GetQuote.js";
import { Bio } from "./components/Bio.js";
import ReactGA from "react-ga";
import { HashRouter, Route, Link } from "react-router-dom";

function App() {
  ReactGA.initialize("G-SV60D026YR");
  ReactGA.pageview(window.location.pathname + window.location.search);

  return (
    <HashRouter>
      <div className="app">
        <nav className="navbar navbar-expand-lg navbar-light">
          <Link to="/" className="nav-link table-hover">
            Home
          </Link>
          <Link to="/bio" className="nav-link table-hover">
            Bio
          </Link>
        </nav>
        <img src={logo} className="App-logo mt-1 mb-1" alt="logo" />
        <div className="app-content">
          <Route exact path="/" component={Getquote} />
          <Route path="/bio" component={Bio} />
        </div>
      </div>
    </HashRouter>
  );
}
export default App;
